<template>
  <mu-appbar>
    <mu-flat-button label="Blog" class="demo-flat-button" icon="home" slot="left" to="/" primary/>
    <mu-flat-button v-bind:label="name" slot="right"/>
    <mu-icon-menu icon="more_vert" slot="right">
      <mu-menu-item v-if="!name" title="登录" @click="login"/>
      <mu-menu-item v-if="!name" title="注册" @click="register" />
      <mu-menu-item v-if="name" title="发表文章" @click="addPost" />
      <mu-divider />
      <mu-menu-item v-if="name" title="注销" @click="logout"/>
    </mu-icon-menu>
  </mu-appbar>
</template>

<script>
  import api from '../services/api'
  export default {
    data: function () {
      return {
        title: 'blog',
        name: ''
      }
    },
    mounted: function () {
      this.$http.get(api.user).then((response) => {
        this.name = response.data.username
      }, (response) => {
        this.name = ''
      })
    },
    methods: {
      'Home': function () {
        alert('home')
      },
      logout: function () {
        this.$cookie.delete('token')
        console.log(this.$cookie.get('token'))
        this.$router.push({name: 'index'})
        window.location.reload()
      },
      login: function () {
        this.$router.push({name: 'login'})
      },
      register: function () {
        this.$router.push({name: 'register'})
      },
      addPost: function () {
        this.$router.push({name: 'addPost'})
      }
    }
  }
</script>



