<template>
  <div class="demo-grid">
    <mu-row gutter>
      <mu-col width="5" tablet="15" desktop="20"></mu-col>
      <mu-col width="90" tablet="50" desktop="50">
        <router-link v-for="post in posts"
                     :to="{ name: 'post',
                      params: {id: post.post_id},
                       query: {url: post.url}}"
                     :key="post.id">
          <moon-article v-bind:post="post"></moon-article>
        </router-link>
      </mu-col>
      <mu-col width="90" tablet="20" desktop="10">
        <moon-slide></moon-slide>
      </mu-col>
      <mu-col width="5" tablet="15" desktop="20"></mu-col>
    </mu-row>
  </div>
</template>

<script>
  import article from '../components/post/articleOutline.vue'
  import slide from '../components/slidebar.vue'
  import api from '../services/api'
  export default {
    data: function () {
      return {
        posts: ''
      }
    },
    mounted: function () {
      this.$http.get(api.posts).then((response) => {
        this.posts = response.data.posts
      })
    },
    components: {
      'moon-slide': slide,
      'moon-article': article
    },
    methods: {
      Like: function () {
        alert(1)
      }
    }
  }
</script>

<style lang="css">
  .demo-grid {
    margin-bottom: 50px;
    margin-top: 8px;
    padding: 8px;
  }
</style>




