import Vue from 'vue'
import Vuex from 'vuex'
import getters from './getter'

Vue.use(Vuex)

const state = {
  colors: ['#e8eaf6', '#e3f2fd', '#e1f5fe', '#e0f7fa', '#a7ffeb']
}

export default new Vuex.Store({
  state,
  getters
})



