<template>
  <mu-flexbox justify="center" orient="vertical" style="margin: 8px;">
    <mu-flexbox-item class="register">
      <mu-card>
        <mu-text-field label="用户名" hintext="用户名最长32个字符" icon="account_circle" type="text" :errorText="error_username" v-model="username" labelFloat/><br/>
        <mu-text-field label="邮箱" hintText="用于登录" icon="mail" type="email" :errorText="error_email" v-model="email"/><br/>
        <mu-text-field label="密码" hintText="密码8-20字符" icon="lock" type="password" :errorText="error_password" v-model="password" labelFloat/><br/>
        <mu-text-field label="地址" hintText="最长64个字符" icon="place" type="text" v-model="location" labelFloat/><br/>
        <mu-text-field label="简介" icon="perm_contact_calendar" type="text" multiLine :rows="3" :rowsMax="6" v-model="about" labelFloat/><br/>
        <mu-card-actions>
          <mu-raised-button label="注册" v-on:click="register" primary/>
        </mu-card-actions>
      </mu-card>
    </mu-flexbox-item>
  </mu-flexbox>
</template>

<script>
  import api from '../services/api'
  export default {
    data: function () {
      return {
        username: '',
        email: '',
        location: '',
        password: '',
        about: '',
        error_username: '',
        error_email: '',
        error_password: ''
      }
    },
    methods: {
      register: function () {
        let flag = true
        if (!this.username) {
          this.error_username = '用户名不能为空'
          flag = false
        }
        if (!this.email) {
          this.error_email = '邮箱不能为空'
          flag = false
        }
        if (!this.password) {
          this.error_password = '密码不能为空'
          flag = false
        }
        if (!flag) return
        this.$http.post(
          api.user,
          {
            email: this.email,
            password: this.password,
            username: this.username,
            location: this.location,
            about: this.about
          }
        ).then((response) => {
          this.$cookie.set(
            'token',
            response.data.token,
            {expires: response.data.expiration + 's'}
          )
        }, (response) => {
          alert('fail')
        })
      }
    }
  }
</script>

<style scoped>
  .register {
    width: 50%;
    text-align: center;
  }
</style>



