<template>
  <div class="article">
    <mu-row>
      <mu-col width="5" tablet="10" desktop="25"></mu-col>
      <mu-col width="90" tablet="80" desktop="50">
        <mu-flexbox orient="vertical">
          <mu-flexbox-item>
            <moon-article v-bind:post="post" v-bind:deletePermission="deletePermission" @deleteEvent="deletePost"></moon-article>
          </mu-flexbox-item>
          <mu-flexbox-item>
            <mu-card v-for="comment in comments" :key="comment.id">
              <mu-card-header :title="comment.author" :subTitle="comment.timestamp">
              <mu-avatar src="/static/uicon.png" slot="avatar"/>
              </mu-card-header>
              <mu-divider />
              <mu-card-text v-html="comment.body_html">
              </mu-card-text>
            </mu-card>
          </mu-flexbox-item>
        </mu-flexbox>
      </mu-col>
      <mu-col width="5" tablet="10" desktop="25"></mu-col>
    </mu-row>
    <moon-dialog :post="post.post_id" @submit="reloadComment"></moon-dialog>
  </div>
</template>

<script>
  import article from '../components/post/article.vue'
  import editor from '../components/comment/editor.vue'
  import dialog from '../components/post/articleDialog.vue'
  import api from '../services/api'
  export default {
    data: function () {
      return {
        post: '',
        comments: '',
        deletePermission: ''
      }
    },
    mounted: function () {
      this.$http.get(this.$route.query.url).then((response) => {
        this.post = response.data.post
        this.comments = response.data.post.comments
        this.deletePermission = response.data.delete_permission
        console.log(response.data.delete_permission)
      }, (response) => {
        alert('Request get fail!')
        console.log(response)
      })
    },
    components: {
      'moon-editor': editor,
      'moon-article': article,
      'moon-dialog': dialog
    },
    methods: {
      reloadComment: function () {
        this.$http.get(
          api.comment,
          {
            params: {
              post: this.post.post_id
            }
          }
        ).then((response) => {
          this.comments = response.data.comments
        })
      },
      deletePost: function () {
        this.$http.delete(
          api.post,
          {
            params: {
              post_id: this.post.post_id
            }
          }
        ).then((response) => {
          this.$router.push({name: 'index'})
        })
      }
    }
  }
</script>
<style scoped>
  .chip {
    margin: 10px !important;
  }
  .article {
    margin-top: 8px;
  }
</style>



